@font-face {
  font-weight: 400;
  font-family: "Oswald";
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "Oswald";
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  background-color: #ffffff;
}
a {
  text-decoration: none;
}
li {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-main {
  margin: 0;
  padding: 0;
}
.page-main__catalog {
  margin: 0;
  padding: 0;
}
.page-main__form {
  margin: 0;
  padding: 0;
}
.page-main__catalog-header--wrapper {
  width: 320px;
  margin: 0 auto;
}
.page-main__header--catalog {
  margin: 15px 0 28px 20px;
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
  color: #111111;
}
.page-main__header--form {
  margin: 0;
  margin-top: 16px;
  margin-left: 20px;
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
  color: #111111;
}
@media (min-width: 768px) {
  .page-main__catalog {
    border: none;
  }
  .page-main__catalog-header--wrapper {
    width: 708px;
  }
  .page-main__header--catalog {
    margin: 0;
    margin-top: 41px;
    font-size: 60px;
  }
  .page-main__header--form {
    margin: 41px 0 25px 29px;
    font-size: 60px;
  }
}
@media (min-width: 1300px) {
  .page-main__catalog {
    max-width: 100%;
  }
  .page-main__catalog-header--wrapper {
    width: 1220px;
    margin: 0 auto;
  }
  .page-main__header--catalog {
    margin: 0;
    margin-top: 75px;
  }
  .page-main__form {
    padding-top: 1px;
  }
  .page-main__header-form--wrapper {
    width: 1280px;
    margin: 0 auto;
  }
  .page-main__header--form {
    margin-top: 74px;
    margin-left: 30px;
  }
}
.button {
  display: inline-block;
  vertical-align: top;
  width: 280px;
  min-height: 40px;
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    width: 249px;
  }
}
@media (min-width: 1300px) {
  .button {
    width: 169px;
  }
}
/* Шапка сайта */
.page-header__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 100%;
  margin: 0;
  padding: 0;
}
.page-header__main-nav--close {
  display: none;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 30px;
  }
  .page-header__main-nav--close {
    display: block;
  }
}
@media (min-width: 1300px) {
  .page-header__index {
    position: relative;
    max-width: 100%;
    height: 696px;
    margin-bottom: 80px;
    background-image: linear-gradient(to left, #64a83b 50%, #ffffff 50%);
  }
  .page-header__index::before {
    position: absolute;
    content: url("../img/index-background-desktop.jpg");
    right: 0;
    width: 650px;
    overflow: hidden;
  }
  .page-header__wrapper {
    width: 1300px;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
    box-sizing: border-box;
  }
  .page-header__main-nav {
    z-index: 2;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .page-header__index::before {
    content: url("img/index-background-desktop@2x.jpg");
  }
}
.header-nav {
  display: flex;
  justify-content: space-between;
  padding: 10px 0 16px 20px;
  border-bottom: 1px solid #ededed;
}
.header-nav__logo {
