  margin-top: 13px;
  margin-right: 23px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("../img/icon-close.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
}
.header-nav__close--hidden {
  display: none;
}
.header-nav__menu {
