    height: 60px;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  border: none;
}
.main-nav__item {
  padding: 23px 2px 18px 0;
  list-style: none;
  text-align: center;
  border-bottom: 1px solid #ededed;
}
